import React, { useState } from 'react';
import LoginHeader from '../Headers/LoginHeader';
import { useForm } from "react-hook-form";
import axios from 'axios'

const Otpform = () => {
    const [otp, setOtp] = useState({})
    const inputStyle = 'm-2 text-center form-control rounded'
    const { register, handleSubmit, setValue, formState: { errors } } = useForm(
        {
            criteriaMode: 'firstError',
            mode: 'onSubmit',
            reValidateMode: 'onChange'
        }
    );
    //handle submit function
    const handleChange = (event) => {
        const name = event.target.name
        const value = event.target.value
        const id = event.target.id
        setOtp(values => ({ ...values, [name]: value }))
        event.target.form.elements[event.target.id].type = "text"
    }

    const inputfocus = (event) => {
        setTimeout((e)=>{
            if (event.key === "Backspace") {
                const next = event.target.tabIndex - 2;
                if (next > -1) {
                    event.target.form.elements[next].focus()
                    event.target.form.elements[event.target.id].type = 'password'
                }
            }
            else {
                if (event.target.tabIndex <= 5) {                
                        event.target.form.elements[event.target.id].type = 'password'
                        event.target.form.elements[event.target.tabIndex].focus()                 
                }
                event.target.form.elements[event.target.id].type = 'password'
            }   
        }, 200)       
    }
     //reset form fields
  const resetForm = () => {
    setValue('value1', '')
    setValue("value2", '')
    setValue('value3', '')
    setValue('value4', '')
    setValue('value5', '')
    setValue('value6', '')
  }

    const onSubmit = (data) => {
        const getOtp = ''.concat(data.value1, data.value2, data.value3, data.value4, data.value5, data.value6)
        alert(getOtp)
        axios({
            method: 'post',
            url: `http://localhost:4000/otp-verification`,
            headers: { 'content-type': 'application/json' },
            data
        }).then(function (response) {
            console.log(response)
        })
            .catch(function (error) {
                console.log('Something goes wrong');
            });
            setOtp({})     
            resetForm()    
    }

    return (
        <>
            <LoginHeader />
            <div class="app-content page-body" id='otp-background'>
                <div class="container height-100 d-flex justify-content-center align-items-center">
                    <div class="position-relative">
                        <div class="card-one p-2 text-center">
                            <form onSubmit={handleSubmit(onSubmit)} class="center-div">
                                <h6>Please enter the one time password <br /> to verify your account</h6>
                                <div class="ot-info"> <span>OTP verification code sended</span></div>
                                <div id="otp" class="inputs d-flex flex-row justify-content-center mt-2">
                                    <input value={otp.value1} id='value1' class={errors.value1 ? inputStyle + " validate-otp": inputStyle} type="password" maxLength="1" tabIndex="1" name='value1'
                                        {...register("value1", { required: true, onChange: (e) => { handleChange(e) } })} onKeyUp={(e) => inputfocus(e)} />
                                    <input value={otp.value2} id='value2' class={errors.value2 ? inputStyle + " validate-otp": inputStyle} type="password" maxLength="1" tabIndex="2" name='value2'
                                        {...register("value2", { required: true, onChange: (e) => { handleChange(e) } })} onKeyUp={(e) => inputfocus(e)} />
                                    <input value={otp.value3} id='value3' class={errors.value3 ? inputStyle + " validate-otp": inputStyle} type="password" maxLength="1" tabIndex="3" name='value3'
                                        {...register("value3", { required: true, onChange: (e) => { handleChange(e) } })} onKeyUp={(e) => inputfocus(e)} />
                                    <input value={otp.value4} id='value4' class={errors.value4 ? inputStyle + " validate-otp": inputStyle} type="password" maxLength="1" tabIndex="4" name='value4'
                                        {...register("value4", { required: true, onChange: (e) => { handleChange(e) } })} onKeyUp={(e) => inputfocus(e)} />
                                    <input value={otp.value5} id='value5' class={errors.value6 ? inputStyle + " validate-otp": inputStyle} type="password" maxLength="1" tabIndex="5" name='value5'
                                        {...register("value5", { required: true, onChange: (e) => { handleChange(e) } })} onKeyUp={(e) => inputfocus(e)} />
                                    <input value={otp.value6} id='value6' class={errors.value6 ? inputStyle + " validate-otp": inputStyle} type="password" maxLength="1" tabIndex="6" name='value6'
                                        {...register("value6", { required: true, onChange: (e) => { handleChange(e) } })} onKeyUp={(e) => inputfocus(e)} />
                                </div>
                                <div class="mt-4">
                                    <button class="px-4 validate">Validate</button>
                                </div>
                            </form>
                            <div class="text-center mt-2">
                                <span class="d-block mobile-text">Don't receive the code?</span>
                                <a class="font-weight-bold text-danger otp-resend"  href="/nettt/#/login">Resend</a>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </>
    );
}

export default Otpform;