import React from 'react';

const LoginHeader = () => {
    return (
        <>
            <div class="header top-header">
                <div class="container-fluid">
                    <div class="d-flex">
                        <a class="header-brand">
                            <img src="assets/images/brand/logo-nettt-s.png" class="header-brand-img desktop-lgo" alt="NETTTSIte logo" />
                            <img src="assets/images/brand/logo.jpg" class="header-brand-img dark-logo" alt="NETTTSIte logo" />
                            <img src="assets/images/brand/logo-nettt-s.png" class="header-brand-img mobile-logo" alt="NETTTSIte logo" />
                            <img src="assets/images/brand/logo.jpg" class="header-brand-img darkmobile-logo" alt="NETTTSIte logo" />
                        </a>
                        <div class="nav-icon-home nav-icon-header" >
                            <a class="navbtn" href="/nettt/#/index" id="goHome">
                                <span class=" nav-span icon"><i class="fa fa-home"></i> </span>
                            </a>
                        </div>
                    </div>
                </div>
            </div>
        </>
    );
}

export default LoginHeader;
